import React from "react"
import Layout from "../components/layout"
import { graphql } from "gatsby"
import SEO from "../components/seo"
const About = props => {
  console.log(props)
  return (
    <div>
      <Layout location={props.location}>
        <h2 className="postTitle">About</h2>
        <p className="postBody">
          I'm a developer enthusiastic about functional programming, and blockchain tech.<br/><br/>
          There's so much innovation taking place in these fields that one can be hard pressed to find good resources
          that explain what's going on at the source code level.<br/><br/>
          This blog is my effort to both learn and help spread knowledge about functional programming and blockchain and is
          targeted towards beginner developers.<br/><br/>
        </p>
      <div className="centerAndBotMargin">
      <a
          href={props.data.site.siteMetadata.social.githubUrl}
          target="_blank"
          rel="noreferrer"
        >
          github
        </a>
        &nbsp;&bull;&nbsp;
        <a
          href={props.data.site.siteMetadata.social.instagramUrl}
          target="_blank"
          rel="noreferrer"
        >
          instagram
        </a>
        </div>
      </Layout>
      <SEO title="About" description="About KV" />
    </div>
  )
}

export const pageQuery = graphql`
  query AboutSummary {
    site {
      siteMetadata {
        social {
          githubUrl,
          instagramUrl
        }
      }
    }
  }
`

export default About
